import React, {Component} from "react";

export default class Documentation extends Component{
  render() {
    return(
        <div>
          <h1>CA3 af Yousef og Janus</h1>
          <p>Vi har lavet en bookstore hvor man kan se sine bger og tilfje flere til en server af bger der lbende opdaterer dem</p>
          <p>Man kan logge ind som bruger og admin og hver har adgang til lidt mere end standard</p>
          <a href="https://github.com/JanusU/ca3"> Github  </a>
            <p> Vi har formet at lave et rest api der kan hente, tilfje og slette en bog samt opdatere en allerede eksisterende bogs information
            </p>
            <p> Desuden har vi lavet de tilhrede funktioner til alle CRUD operationer i frontend delen(se BookStore.js)</p>
            <p>Derudover net vi at implementere UI, s man kan se en liste af gemte bger samt mulighed for at tilfje en ny bog, som s bliver gemt i databasen.
                Dog lykkedes det os ikke, p grund af tidsfristen, at implementere UI s man kan slette eller redigere en bog.(Metoderne er lavet i BookStore,js) </p>


        </div>

        )
  }
}


